import {get,post} from './../axiosConfig'
import reHttp from './../http'


/********************  ********************/

/**
 * 
 */
export function getReserveCrossList (param) {
    return post(`${reHttp}/frontend/cross/list`,param)
}
/**
 * 
 */
export function getReserveCrossDetails (param) {
    return post(`${reHttp}/frontend/cross/details`,param)
}



export default {
    getReserveCrossList,
    getReserveCrossDetails
}


