<template>
    <div v-loading="true" style="height:100%;display: contents;">{{token_}}</div>
</template>

<script >
import Vue from "vue";
import { mapState, mapMutations, mapActions } from "vuex";
import dappSDK from "dappsdk";

export default {
    name: "index",
    data() {
        return {};
    },
    components: {},
    computed: {
        /*catch store.data*/
        ...mapState({
            token_: state => state.token_
        })
    },
    mounted() {
        window.rePage = this.rePage;
        // this.rePage('order')
        //token
        this.Initialization_();
    },
    methods: {
        ...mapMutations(["settoken"]),
        /**
         * android
         * @param 1 
         * @param 2 .
         * @param 3 .
         * @param 2 callback
         * @returns  {}
         */
        Initialization_() {
            const that = this;
            dappSDK.exec("system", "initWebview", {}, function(err, data) {
                if (err) {
                    var code = error.code;
                    if (code == "-10001") {
                        console.log(
                            "namespace, sdknative, appsdk"
                        );
                    }
                    if (code == "-10002") {
                        console.log(
                            "function, sdknative, appsdk"
                        );
                    }
                    if (code == "-10003") {
                        console.log("params");
                    }
                    if (code == "-10004") {
                        console.log("params");
                    }
                    console.log(code);
                    // , return
                    return alert("Error: " + err.message);
                }

                that.settoken(data.token); //token
                that.rePage(data.page);
                console.log(
                    data,
                    "webview system initWebview console message~~~~~~~"
                );
            });
        },

        /**
         * android
         * @param 1 
         * @param 2 .
         * @param 3 .
         * @param 2 callback
         * @returns  {}
         */
        currentPage(pagename) {
            const that = this;
            let params={
                'page' : pagename
            }
            dappSDK.exec("system", "currentPage", params, function(err, data) {
                if (err) {
                    var code = error.code;
                    if (code == "-10001") {
                        console.log(
                            "namespace, sdknative, appsdk"
                        );
                    }
                    if (code == "-10002") {
                        console.log(
                            "function, sdknative, appsdk"
                        );
                    }
                    if (code == "-10003") {
                        console.log("params");
                    }
                    if (code == "-10004") {
                        console.log("params");
                    }
                    console.log(code);
                    // , return
                    return alert("Error: " + err.message);
                }
            });
        },

        /**
         * webviewfunc
         * @param 1 url
         * @returns  void
         */
        rePage(url) {
            const that = this;
            switch (url) {
                case "order":
                    that.$router.push({
                        path: "/orderList"
                    });
                    that.currentPage('order')
                    break;
                case "handOverDuty":
                    that.$router.push({
                        path: "/handOverDuty"
                    });
                    that.currentPage('handOverDuty')
                break;
                default:
                    that.$router.push({
                        path: "/orderList"
                    });
                    that.currentPage('order')
                    break;
            }
        }
    }
};
</script>



